	  import {UniPopup} from 'components/uni/uni-popup/uni-popup'
	  import {KsSort} from 'components/ks-components/sort/sort'
	  import {ErrorTkHome,GetCountErrorTk} from 'api/exam'
    export default {
		components:{
			KsSort,
			UniPopup
		},
        data() {
        return {
          allData: {
			  today_error:0,
			  zhunquelv:0,
			  error_num:0
		  },
          allDataList: [],
          showSort: false,
          categoryid: 0,
          type: 2,
		   baseUrl:this.$store.state.SRC+"/config/app"
        };
      },
      methods: {
		  close(e){
		  	this.showSort = e.show
		  },
        openSort() {
          this.showSort = true;
        },
        closeSort() {
          this.showSort = false;
        },
        errorTkHome() {
          ErrorTkHome({
            categoryid: this.categoryid
          }).then(res=> {
            if (res.result) {
                this.allData = res.data;
            }
          }),
           GetCountErrorTk({
              categoryid: this.categoryid
            }).then(res=> {
              if (res.result) {
                this.allDataList = res.data;
              }
            });
        }
      },
	  onLoad(query) {
	  	this.categoryid = query.categoryid
	  },
      onShow() {

        this.$store.ready(state=>{
          if(state.isLogin == 1){
            this.errorTkHome();
          }else{
            uni.navigateTo({
            	url:'/pages/user/login/index'
            })
          }
          
        })
      }
    };
  